import React, { useState } from 'react'

const SortPopUp = React.memo(({ items, activeSort, onClickSort}) => {

  const [openPopUp, setOpenPopUp] = useState(false)
  
  const activeSelectedItem = items.find((obj) => obj.type === activeSort).name
  
  const selectedItem = (i) => {
    if (onClickSort) {
      onClickSort(i)
    }
    setOpenPopUp(false)
  }
 
  
  
  //создаем функцию которая хранит ссылку на дом-елемент
  const sortReference = React.useRef()
  
  const togglePopUp = () => setOpenPopUp(!openPopUp)
  
  //настраиваем функцию которая хранит ссылку на дом елемент
  const outsideClickHandle = e => {
    if (!e.path.includes(sortReference.current)) {
      setOpenPopUp(false)
    }
  }
  
  //отлавливаем клик по области body, первый рендер без зависимостей!
  React.useEffect(() => {
   document.body.addEventListener('click', outsideClickHandle )
 }, [])

  return (
    //ccылка на блок кода храниться в  sortReference
    <div ref={sortReference} className="sort">
      <div className="sort__label">
        <svg className={openPopUp ? 'rotate' : ''}
          width="10"
          height="6"
          viewBox="0 0 10 6"
          fill="none"
          xmlns="http://www.w3.org/2000/svg">
          <path
            d="M10 5C10 5.16927 9.93815 5.31576 9.81445 5.43945C9.69075 5.56315 9.54427 5.625 9.375 5.625H0.625C0.455729 5.625 0.309245 5.56315 0.185547 5.43945C0.061849 5.31576 0 5.16927 0 5C0 4.83073 0.061849 4.68424 0.185547 4.56055L4.56055 0.185547C4.68424 0.061849 4.83073 0 5 0C5.16927 0 5.31576 0.061849 5.43945 0.185547L9.81445 4.56055C9.93815 4.68424 10 4.83073 10 5Z"
            fill="#2C2C2C"
          />
        </svg>
        <b>filter by:</b>
        <span onClick={togglePopUp}>{activeSelectedItem}</span>
      </div>
      {openPopUp && <div className="sort__popup">
        <ul>
          
          {items &&
            items.map((obj, i) => <li
              className={activeSort === obj.type ? 'active' : ''}
            key={obj.type}
            onClick={() => selectedItem(obj)}>{obj.name}</li>)}
           
        </ul>
      </div>}
    </div>
  )
})

SortPopUp.defaultPpops = {
  items: []
}

export default SortPopUp
